.uni-card {
  position: relative;
}

.uni-card__media img {
  width: 100%;
}

.uni-card__body {
  padding-top: 20px;
}

.uni-card__description {
  margin-top: 15px;
}

.uni-card__description:first-child {
  margin-top: 0;
}

.uni-card__description:last-child {
  margin-bottom: 0;
}

.uni-card__footer {
  padding-top: 20px;
}

.uni-card__link a {
  text-indent: 200%;
  white-space: nowrap;
  position: absolute;
  inset: 0;
  overflow: hidden;
}

